.cart-main {
    display: flex;
    justify-content: space-around;
    align-items: center;
    /* width: 100%; */

}

.product-img img {
    width: 100px;

}

.description {
    width: 20vw;
    /* height: 10vw; */
    text-overflow: ellipsis;

}

.title {
    width: 8rem;
}

.items-info {
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 20px;
}

.cart_content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}